.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.social a {
    font-size: 27px;
    margin: 0.5rem;
}

.social a .bx {
    padding: 5px;
    color: #fff;
    background: #000000;
    border-radius: 50%;
}

.social a .bx:hover {
    background: var(--main-color)
}

.links {
    margin: 1rem 0 1rem;
}

.links a {
    font-size: 1rem;
    font-weight: 500;
    color: var(--second-color);
    padding: 1rem;
}

.links a:hover {
    color: var(--main-color);
}

.contact p {
    text-align: center;
}

@media (max-width: 991px) {
    section {
        padding: 50px 4%;
    }
}

@media (max-width: 768px) {
    section {
        padding: 50px 7%;
    }
}

@media (max-width: 364px) {
    .links {
        display: flex;
        flex-direction: column;
    }
}